html {
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::after,
*::before {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  margin: 0;
  height: 100%;
  background-color: white;
  font-style: normal;
  font-weight: 500;
  font-family: "Poppins", "Arial", sans-serif; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

.wrapper {
  margin: 0 auto;
  max-width: 1366px;
  min-height: 100vh;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column; }

.container {
  padding: 0 45px;
  margin: 0 auto;
  max-width: 1260px; }
  @media (max-width: 767px) {
    .container {
      padding: 0 15px; } }

/* Header */
.header {
  padding: 10px 0;
  color: white;
  background-color: black; }

/* Main */
.main {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto; }

/* Footer */
.footer {
  color: white;
  background-color: black; }
